import OpenAI from 'openai';
import { PrismaClient } from '@prisma/client';
import { queueFAQEmbedding } from './embeddingWorker';

const prisma = new PrismaClient();
const openai = new OpenAI({ apiKey: process.env.OPENAI_API_KEY });

// FAQÊèêÊ°à„ÅÆÂûãÂÆöÁæ©
export interface FAQSuggestion {
  id: string;
  originalMessage: string;
  suggestedQuestion: string;
  suggestedAnswer: string;
  confidence: number;
  count: number;
  lastAskedAt: string;
  priority: 'high' | 'medium' | 'low';
}

// Êú™ÂõûÁ≠î„É°„ÉÉ„Çª„Éº„Ç∏„ÅÆÂûãÂÆöÁæ©
interface UnansweredMessage {
  id: string;
  message: string;
  count: number;
  lastAskedAt: Date;
  confidence: number | null;
}

// Êú™ÂõûÁ≠î„É°„ÉÉ„Çª„Éº„Ç∏„Åã„ÇâFAQ„ÇíÁîüÊàê
async function generateFAQFromMessage(
  message: string,
  organizationName: string,
  relatedContext?: string
): Promise<{ question: string; answer: string; confidence: number }> {
  try {
    const systemPrompt = `„ÅÇ„Å™„Åü„ÅØ${organizationName}„ÅÆ„Ç´„Çπ„Çø„Éû„Éº„Çµ„Éù„Éº„ÉàÂ∞ÇÈñÄÂÆ∂„Åß„Åô„ÄÇ

‰ª•‰∏ã„ÅÆ„Ç¨„Ç§„Éâ„É©„Ç§„É≥„Å´Âæì„Å£„Å¶„ÄÅ„É¶„Éº„Ç∂„Éº„Åã„Çâ„ÅÆË≥™Âïè„ÇíÈÅ©Âàá„Å™FAQ„Å´Â§âÊèõ„Åó„Å¶„Åè„Å†„Åï„ÅÑÔºö

1. „É¶„Éº„Ç∂„Éº„ÅÆÊÑèÂõ≥„ÇíÊòéÁ¢∫„Å´ÁêÜËß£„Åó„ÄÅ‰∏ÄËà¨ÁöÑ„Å™Ë≥™Âïè„Å®„Åó„Å¶ÂÜçÊßãÊàê„Åô„Çã
2. ÂõûÁ≠î„ÅØÂÖ∑‰ΩìÁöÑ„ÅßÂÆüÁî®ÁöÑ„Å™ÂÜÖÂÆπ„Å´„Åô„Çã
3. Êó•Êú¨Ë™û„ÅßËá™ÁÑ∂„Å™Ë°®Áèæ„Çí‰ΩøÁî®„Åô„Çã
4. ÊÉÖÂ†±„Åå‰∏çË∂≥„Åó„Å¶„ÅÑ„ÇãÂ†¥Âêà„ÅØ‰∏ÄËà¨ÁöÑ„Å™ÂõûÁ≠î„Éë„Çø„Éº„É≥„ÇíÊèê‰æõ„Åô„Çã
5. Ë≥™Âïè„ÅØ‰ªñ„ÅÆ„É¶„Éº„Ç∂„Éº„Å´„ÇÇÂΩπÁ´ã„Å§„Çà„ÅÜ„Å´Ê±éÁî®Âåñ„Åô„Çã

${relatedContext ? `Èñ¢ÈÄ£ÊÉÖÂ†±:\n${relatedContext}\n` : ''}

„É¶„Éº„Ç∂„Éº„Åã„Çâ„ÅÆË≥™Âïè: "${message}"

‰ª•‰∏ã„ÅÆJSON„Éï„Ç©„Éº„Éû„ÉÉ„Éà„ÅßÂøúÁ≠î„Åó„Å¶„Åè„Å†„Åï„ÅÑÔºö
{
  "question": "Êï¥ÁêÜ„Åï„Çå„ÅüË≥™Âïè",
  "answer": "Ë©≥Á¥∞„Å™ÂõûÁ≠î",
  "confidence": 0.0-1.0„ÅÆ‰ø°È†ºÂ∫¶„Çπ„Ç≥„Ç¢
}`;

    const response = await openai.chat.completions.create({
      model: 'gpt-4o',
      messages: [{ role: 'system', content: systemPrompt }],
      temperature: 0.3,
      max_tokens: 800,
    });

    const content = response.choices[0]?.message?.content;
    if (!content) {
      throw new Error('No response from OpenAI');
    }

    try {
      const parsed = JSON.parse(content);
      return {
        question: parsed.question || message,
        answer:
          parsed.answer ||
          'Ë©≥Á¥∞„Å´„Å§„ÅÑ„Å¶„ÅØ„ÄÅ„Çµ„Éù„Éº„Éà„ÉÅ„Éº„É†„Åæ„Åß„ÅäÂïè„ÅÑÂêà„Çè„Åõ„Åè„Å†„Åï„ÅÑ„ÄÇ',
        confidence: Math.min(Math.max(parsed.confidence || 0.5, 0), 1),
      };
    } catch (parseError) {
      // JSONËß£Êûê„Å´Â§±Êïó„Åó„ÅüÂ†¥Âêà„ÅÆ„Éï„Ç©„Éº„É´„Éê„ÉÉ„ÇØ
      return {
        question: message,
        answer: 'Ë©≥Á¥∞„Å´„Å§„ÅÑ„Å¶„ÅØ„ÄÅ„Çµ„Éù„Éº„Éà„ÉÅ„Éº„É†„Åæ„Åß„ÅäÂïè„ÅÑÂêà„Çè„Åõ„Åè„Å†„Åï„ÅÑ„ÄÇ',
        confidence: 0.3,
      };
    }
  } catch (error) {
    console.error('Failed to generate FAQ from message:', error);
    throw error;
  }
}

// È°û‰ºº„ÅÆË≥™Âïè„Çí„ÇØ„É©„Çπ„Çø„É™„É≥„Ç∞
function clusterSimilarMessages(
  messages: UnansweredMessage[]
): UnansweredMessage[][] {
  const clusters: UnansweredMessage[][] = [];
  const used = new Set<string>();

  for (const message of messages) {
    if (used.has(message.id)) continue;

    const cluster = [message];
    used.add(message.id);

    // Á∞°Âçò„Å™È°û‰ººÂ∫¶Âà§ÂÆöÔºàÂÆüÈöõ„Å´„ÅØ„Çà„ÇäÈ´òÂ∫¶„Å™„Ç¢„É´„Ç¥„É™„Ç∫„É†„Çí‰ΩøÁî®Ôºâ
    for (const otherMessage of messages) {
      if (used.has(otherMessage.id)) continue;

      const similarity = calculateSimpleSimilarity(
        message.message,
        otherMessage.message
      );
      if (similarity > 0.7) {
        cluster.push(otherMessage);
        used.add(otherMessage.id);
      }
    }

    clusters.push(cluster);
  }

  return clusters;
}

// Á∞°Âçò„Å™ÊñáÂ≠óÂàóÈ°û‰ººÂ∫¶Ë®àÁÆó
function calculateSimpleSimilarity(str1: string, str2: string): number {
  const words1 = str1.toLowerCase().split(/\s+/);
  const words2 = str2.toLowerCase().split(/\s+/);

  const intersection = words1.filter((word) => words2.includes(word));
  const union = [...new Set([...words1, ...words2])];

  return intersection.length / union.length;
}

// ÂÑ™ÂÖàÂ∫¶„ÇíË®àÁÆó
function calculatePriority(
  count: number,
  confidence: number,
  recency: number
): 'high' | 'medium' | 'low' {
  const score = count * 0.4 + confidence * 0.3 + recency * 0.3;

  if (score > 0.7) return 'high';
  if (score > 0.4) return 'medium';
  return 'low';
}

// FAQÊèêÊ°à„ÇíÁîüÊàê
export async function generateFAQSuggestions(
  organizationId: string,
  limit: number = 50
): Promise<FAQSuggestion[]> {
  try {
    console.log(
      `Generating FAQ suggestions for organization: ${organizationId}`
    );

    // ÁµÑÁπîÊÉÖÂ†±„ÇíÂèñÂæó
    const organization = await prisma.organization.findUnique({
      where: { id: organizationId },
    });

    if (!organization) {
      throw new Error(`Organization not found: ${organizationId}`);
    }

    // Êú™Âá¶ÁêÜ„ÅÆÊú™ÂõûÁ≠î„É°„ÉÉ„Çª„Éº„Ç∏„ÇíÂèñÂæó
    const unansweredMessages = await prisma.unansweredMessage.findMany({
      where: {
        organizationId,
        isProcessed: false,
        count: { gte: 2 }, // 2Âõû‰ª•‰∏äË≥™Âïè„Åï„Çå„Åü„ÇÇ„ÅÆ„ÇíÂØæË±°
      },
      orderBy: [{ count: 'desc' }, { lastAskedAt: 'desc' }],
      take: limit * 2, // Â§ö„ÇÅ„Å´ÂèñÂæó„Åó„Å¶„ÇØ„É©„Çπ„Çø„É™„É≥„Ç∞Âæå„Å´Áµû„ÇäËæº„Åø
    });

    console.log(`Found ${unansweredMessages.length} unanswered messages`);

    if (unansweredMessages.length === 0) {
      return [];
    }

    // È°û‰ººË≥™Âïè„Çí„ÇØ„É©„Çπ„Çø„É™„É≥„Ç∞
    const clusters = clusterSimilarMessages(unansweredMessages);

    // ÂêÑ„ÇØ„É©„Çπ„Çø„Éº„Åã„Çâ‰ª£Ë°®ÁöÑ„Å™Ë≥™Âïè„ÇíÈÅ∏Êäû
    const suggestions: FAQSuggestion[] = [];

    for (const cluster of clusters.slice(0, limit)) {
      try {
        // „ÇØ„É©„Çπ„Çø„Éº„ÅÆ‰∏≠„ÅßÊúÄ„ÇÇÈ†ªÂ∫¶„ÅåÈ´ò„ÅÑ„É°„ÉÉ„Çª„Éº„Ç∏„Çí‰ª£Ë°®„Å®„Åó„Å¶ÈÅ∏Êäû
        const representative = cluster.reduce((prev, current) =>
          current.count > prev.count ? current : prev
        );

        // „ÇØ„É©„Çπ„Çø„ÉºÂÖ®‰Ωì„ÅÆÁµ±Ë®à
        const totalCount = cluster.reduce((sum, msg) => sum + msg.count, 0);
        const avgConfidence =
          cluster.reduce((sum, msg) => sum + (msg.confidence || 0), 0) /
          cluster.length;

        // ÊúÄÊñ∞„ÅÆÊó•‰ªò
        const latestDate = cluster.reduce((latest, msg) =>
          new Date(msg.lastAskedAt) > new Date(latest.lastAskedAt)
            ? msg
            : latest
        ).lastAskedAt;

        // Èñ¢ÈÄ£„Ç≥„É≥„ÉÜ„Ç≠„Çπ„Éà„ÇíÊßãÁØâÔºà„ÇØ„É©„Çπ„Çø„ÉºÂÜÖ„ÅÆ‰ªñ„ÅÆ„É°„ÉÉ„Çª„Éº„Ç∏Ôºâ
        const relatedMessages = cluster
          .filter((msg) => msg.id !== representative.id)
          .map((msg) => msg.message)
          .slice(0, 3)
          .join(', ');

        // AI„Çí‰Ωø„Å£„Å¶FAQ„ÇíÁîüÊàê
        const { question, answer, confidence } = await generateFAQFromMessage(
          representative.message,
          organization.name,
          relatedMessages
        );

        // ÂÑ™ÂÖàÂ∫¶„ÇíË®àÁÆóÔºàÈ†ªÂ∫¶„ÄÅ‰ø°È†ºÂ∫¶„ÄÅÊúÄÊñ∞ÊÄß„ÇíËÄÉÊÖÆÔºâ
        const recency = Math.max(
          0,
          1 -
            (Date.now() - new Date(latestDate).getTime()) /
              (30 * 24 * 60 * 60 * 1000)
        );
        const priority = calculatePriority(
          Math.min(totalCount / 10, 1),
          avgConfidence,
          recency
        );

        suggestions.push({
          id: representative.id,
          originalMessage: representative.message,
          suggestedQuestion: question,
          suggestedAnswer: answer,
          confidence,
          count: totalCount,
          lastAskedAt: latestDate.toISOString(),
          priority,
        });

        // APIÂà∂Èôê„ÇíËÄÉÊÖÆ„Åó„Å¶Â∞ë„ÅóÂæÖÊ©ü
        await new Promise((resolve) => setTimeout(resolve, 100));
      } catch (error) {
        console.error(`Failed to generate suggestion for cluster:`, error);
        continue;
      }
    }

    // ÂÑ™ÂÖàÂ∫¶„Åß„ÇΩ„Éº„Éà
    suggestions.sort((a, b) => {
      const priorityOrder = { high: 3, medium: 2, low: 1 };
      return (
        priorityOrder[b.priority] - priorityOrder[a.priority] ||
        b.count - a.count
      );
    });

    console.log(`Generated ${suggestions.length} FAQ suggestions`);
    return suggestions.slice(0, limit);
  } catch (error) {
    console.error('Failed to generate FAQ suggestions:', error);
    throw error;
  }
}

// FAQÊèêÊ°à„ÇíÊâøË™ç„Åó„Å¶FAQ„Å´Â§âÊèõ
export async function approveFAQSuggestion(
  suggestionId: string,
  organizationId: string,
  overrides?: { question?: string; answer?: string; weight?: number }
): Promise<string> {
  try {
    // Êú™ÂõûÁ≠î„É°„ÉÉ„Çª„Éº„Ç∏„ÇíÂèñÂæó
    const unansweredMessage = await prisma.unansweredMessage.findFirst({
      where: {
        id: suggestionId,
        organizationId,
      },
    });

    if (!unansweredMessage) {
      throw new Error(`Unanswered message not found: ${suggestionId}`);
    }

    // FAQ„ÇíÁîüÊàêÔºà„Ç™„Éº„Éê„Éº„É©„Ç§„Éâ„Åå„ÅÇ„Çå„Å∞ÈÅ©Áî®Ôºâ
    let question = overrides?.question;
    let answer = overrides?.answer;

    if (!question || !answer) {
      const organization = await prisma.organization.findUnique({
        where: { id: organizationId },
      });

      const generated = await generateFAQFromMessage(
        unansweredMessage.message,
        organization?.name || 'AI Chat'
      );

      question = question || generated.question;
      answer = answer || generated.answer;
    }

    // FAQ„Çí‰ΩúÊàê
    const faq = await prisma.fAQ.create({
      data: {
        organizationId,
        question,
        answer,
        weight:
          overrides?.weight || Math.max(100 - unansweredMessage.count, 10),
        isActive: true,
        timesUsed: 0,
      },
    });

    // Êú™ÂõûÁ≠î„É°„ÉÉ„Çª„Éº„Ç∏„ÇíÂá¶ÁêÜÊ∏à„Åø„Å´„Éû„Éº„ÇØ
    await prisma.unansweredMessage.update({
      where: { id: suggestionId },
      data: {
        isProcessed: true,
        suggestedQuestion: question,
        suggestedAnswer: answer,
      },
    });

    // „Ç®„É≥„Éô„Éá„Ç£„É≥„Ç∞ÁîüÊàê„Çí„Ç≠„É•„Éº„Å´ËøΩÂä†
    await queueFAQEmbedding(faq.id, organizationId);

    // È°û‰ºº„ÅÆÊú™ÂõûÁ≠î„É°„ÉÉ„Çª„Éº„Ç∏„ÇÇÂá¶ÁêÜÊ∏à„Åø„Å´„Éû„Éº„ÇØ
    const similarMessages = await prisma.unansweredMessage.findMany({
      where: {
        organizationId,
        isProcessed: false,
        message: {
          contains: unansweredMessage.message.split(' ')[0], // ÊúÄÂàù„ÅÆÂçòË™û„ÅßÁ∞°Âçò„Å™È°û‰ººÂà§ÂÆö
          mode: 'insensitive',
        },
      },
      take: 5,
    });

    if (similarMessages.length > 0) {
      await prisma.unansweredMessage.updateMany({
        where: {
          id: { in: similarMessages.map((msg) => msg.id) },
        },
        data: {
          isProcessed: true,
        },
      });
    }

    console.log(`Approved FAQ suggestion: ${question}`);
    return faq.id;
  } catch (error) {
    console.error('Failed to approve FAQ suggestion:', error);
    throw error;
  }
}

// FAQÊèêÊ°à„ÇíÂç¥‰∏ã
export async function rejectFAQSuggestion(
  suggestionId: string,
  organizationId: string,
  reason?: string
): Promise<void> {
  try {
    await prisma.unansweredMessage.update({
      where: { id: suggestionId },
      data: {
        isProcessed: true,
        // Âç¥‰∏ãÁêÜÁî±„Çí„É°„É¢„Å®„Åó„Å¶‰øùÂ≠òÔºàÂøÖË¶Å„Å´Âøú„Åò„Å¶„Çπ„Ç≠„Éº„ÉûÊã°ÂºµÔºâ
      },
    });

    console.log(`Rejected FAQ suggestion: ${suggestionId}, reason: ${reason}`);
  } catch (error) {
    console.error('Failed to reject FAQ suggestion:', error);
    throw error;
  }
}

// ÈÄ±Ê¨°„Éê„ÉÉ„ÉÅ„ÅßFAQÊèêÊ°à„ÇíÁîüÊàê
export async function runWeeklyFAQSuggestionBatch(): Promise<void> {
  try {
    console.log('üîÑ Running weekly FAQ suggestion batch...');

    // ÂÖ®ÁµÑÁπî„ÇíÂèñÂæó
    const organizations = await prisma.organization.findMany({
      select: { id: true, name: true },
    });

    for (const org of organizations) {
      try {
        const suggestions = await generateFAQSuggestions(org.id);
        console.log(
          `Generated ${suggestions.length} suggestions for ${org.name}`
        );
      } catch (error) {
        console.error(`Failed to generate suggestions for ${org.name}:`, error);
      }
    }

    console.log('‚úÖ Weekly FAQ suggestion batch completed');
  } catch (error) {
    console.error('‚ùå Weekly FAQ suggestion batch failed:', error);
  }
}

// FAQÊèêÊ°à„ÅÆÁµ±Ë®à„ÇíÂèñÂæó
export async function getFAQSuggestionStats(organizationId: string) {
  try {
    const [totalUnanswered, processed, highPriority] = await Promise.all([
      prisma.unansweredMessage.count({
        where: { organizationId, isProcessed: false },
      }),
      prisma.unansweredMessage.count({
        where: { organizationId, isProcessed: true },
      }),
      prisma.unansweredMessage.count({
        where: {
          organizationId,
          isProcessed: false,
          count: { gte: 5 }, // 5Âõû‰ª•‰∏äË≥™Âïè„Åï„Çå„ÅüÈ´òÂÑ™ÂÖàÂ∫¶È†ÖÁõÆ
        },
      }),
    ]);

    return {
      totalUnanswered,
      processed,
      highPriority,
      processingRate:
        totalUnanswered > 0 ? processed / (processed + totalUnanswered) : 0,
    };
  } catch (error) {
    console.error('Failed to get FAQ suggestion stats:', error);
    return {
      totalUnanswered: 0,
      processed: 0,
      highPriority: 0,
      processingRate: 0,
    };
  }
}
